.gpt3__features {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.gpt3__features-heading{
  display: flex;
  flex: 1;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;

  text-align: left;
  margin-right: 5rem;
}

.gpt3__features-heading h1{
  color: #FFF;
font-family: var(--font-family);
font-size: 34px;
font-weight: 800;
line-height: 45px;
}

.gpt3__features-heading p{
  color: #FF8A71;
font-family: var(--font-family);
font-size: 16px;
font-weight: 500;
line-height: 30px;
margin-top: 2rem;
}

.gpt3__features-container{
  display: flex;
  flex: 1.5;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

@media screen and (max-width: 990px) {
    .gpt3__features {
        flex-direction: column;
    }

    .gpt3__features-heading {
        margin: 0 0 2rem 0;
    }
}


@media screen and (max-width: 550px) {
    .gpt3__features-heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
}